import { cloneDeep } from 'lodash-es';

/**
 * è½¬æ¢åçé¡ºåº
 * @param columns-ææåæ°æ®
 * @param direction-left|right å·¦åºå®æèå³åºå®
 */
export const convertColumns = (columns, direction) => {
    const tempFixedColumns = [];
    const tempNotFixedColumns = [];
    columns.forEach(ele => {
        if (ele.fixed && (ele.fixed === direction || ele.fixed.type === direction)) {
            tempFixedColumns.push(ele);
        } else {
            tempNotFixedColumns.push(ele);
        }
    });
    return tempFixedColumns.concat(tempNotFixedColumns);
};
/**
 *  å¯¹è±¡æèæ°ç»æ·±æ·è´
 * @param data æºæ°æ®
 */
export const deepCopy = (data) => {
    return cloneDeep(data);
};
export const toUpper = (source: string) => {
    const jsonObj = JSON.parse(source.replace(/\\/g, ''));
    return deepCopy(jsonObj);
};


