import { Component, Input, ContentChild, TemplateRef } from '@angular/core';
import { DataColumn } from '@farris/ui-common/column';
@Component({
    selector: 'datatable-footer',
    template:
        `
    <div class="table"
    [class.table-hover]="hover">
        <ng-container *ngIf="!tableFooter">
                <table class="table table-hover">
                        <colgroup>
                            <col class="dt-checkbox-cell" />
                            <col *ngFor="let col of columns" [style.width]="col.width + 'px'" />
                        </colgroup>
                        <thead>
                            <tr>
                                <th class="dt-checkbox-cell">
                                    
                                </th>
                                <th  *ngFor="let col of columns" [attr.align]="col.align" >{{ col.title }}</th>
                            </tr>
                        </thead>
                    </table>
        </ng-container>
</div>
    `
})
export class DatatableFooterComponent {
    @Input() hover: boolean;
    @Input() columns: DataColumn[];
    // è¡¨æ ¼ footer å¯æ·»å æ¨¡æ¿
    @Input()
    @ContentChild(TemplateRef) tableFooter: TemplateRef<any>;
    constructor() {
    }
}
